// components/ChatBox.tsx
import React, { useState, useEffect, useRef } from 'react';
import { ArrowUpCircle, User, Brain } from 'lucide-react'; // For icons

// Interface for the props of the ChatInput component
interface ChatInputProps {
  onSendMessage: (message: string) => void;
  isLoading: boolean; // To disable input/button while loading
}

// Gemini-style ChatInput component
const ChatInput: React.FC<ChatInputProps> = ({ onSendMessage, isLoading }) => {
  const [inputValue, setInputValue] = useState('');
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  const handleInputChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    setInputValue(event.target.value);
    // Auto-resize textarea height
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto'; // Reset height
      textareaRef.current.style.height = `${textareaRef.current.scrollHeight}px`; // Set to scroll height
    }
  };

  const handleSend = () => {
    if (inputValue.trim() && !isLoading) {
      onSendMessage(inputValue.trim());
      setInputValue('');
      if (textareaRef.current) {
        textareaRef.current.style.height = 'auto';
      }
    }
  };

  const handleKeyDown = (event: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (event.key === 'Enter' && !event.shiftKey) {
      event.preventDefault();
      handleSend();
    }
  };

  return (
    // This is the bar at the bottom of the chat, containing the input pill. Now flex to center the pill.
    <div className="p-3 sm:p-4 bg-white border-t border-slate-200 flex justify-center">
      {/* This is the "pill" - a rounded container for the textarea and send button. Styling updated as per request. */}
      <div className="flex items-end gap-2 p-3 sm:p-3.5 bg-slate-800 border border-slate-600 rounded-3xl focus-within:ring-2 focus-within:ring-indigo-500 focus-within:border-indigo-500 transition-shadow w-full max-w-xl"> 
        <textarea
          ref={textareaRef}
          rows={1}
          className="flex-1 py-3.5 px-3 bg-transparent border-none focus:ring-0 resize-none overflow-y-auto text-sm text-white placeholder-slate-400 max-h-48" 
          placeholder="Enter your message..."
          value={inputValue}
          onChange={handleInputChange}
          onKeyDown={handleKeyDown}
          disabled={isLoading}
        />
        <button
          onClick={handleSend}
          disabled={!inputValue.trim() || isLoading}
          className={`p-2 rounded-full transition-colors duration-150 ease-in-out
                      ${(inputValue.trim() && !isLoading) ? 'bg-indigo-600 text-white hover:bg-indigo-700' : 'bg-slate-600 text-slate-400 cursor-not-allowed'}`} // Adjusted disabled state colors
          aria-label="Send message"
        >
          <ArrowUpCircle size={22} />
        </button>
      </div>
    </div>
  );
};

interface Message {
  id: string;
  text: string;
  sender: 'user' | 'ai' | 'error';
  modelUsed?: string; // To display which model responded
}

// Props for the main ChatBox component
interface ChatBoxProps {
  selectedModel: string;
  apiKey: string;
  persona: string;
  customPrompt: string;
}

export const ChatBox: React.FC<ChatBoxProps> = ({
  selectedModel,
  apiKey,
  persona,
  customPrompt,
}) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSendMessageFromChatBox = async (messageToSend: string) => {
    if (!messageToSend.trim()) return;

    setIsLoading(true);
    const userMessage: Message = {
      id: Date.now().toString() + '-user',
      text: messageToSend,
      sender: 'user',
    };
    const thinkingMessage: Message = {
      id: Date.now().toString() + '-thinking',
      text: 'Thinking...',
      sender: 'ai',
      modelUsed: selectedModel,
    };

    setMessages(prevMessages => [...prevMessages, userMessage, thinkingMessage]);

    try {
      const res = await fetch('http://localhost:8000/chat', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          message: messageToSend,
          model: selectedModel,
          api_key: apiKey,
          persona,
          custom_prompt: customPrompt,
        }),
      });

      if (!res.ok) {
        let errorText = `Error: ${res.status} ${res.statusText}`;
        try {
          const errorData = await res.json();
          errorText = errorData.detail || errorData.error || errorText;
        } catch (e) { /* Ignore parsing error */ }
        
        setMessages(prevMessages =>
          prevMessages.map(msg =>
            msg.id === thinkingMessage.id ? { ...msg, text: errorText, sender: 'error' } : msg
          )
        );
        setIsLoading(false);
        return;
      }

      const data = await res.json();
      setMessages(prevMessages =>
        prevMessages.map(msg =>
          msg.id === thinkingMessage.id
            ? { ...msg, text: data.response || 'Received an empty response.', sender: 'ai' }
            : msg
        )
      );

    } catch (error) {
      console.error("Chat API call failed:", error);
      setMessages(prevMessages =>
        prevMessages.map(msg =>
          msg.id === thinkingMessage.id
            ? { ...msg, text: 'Network error. Could not reach the server.', sender: 'error' }
            : msg
        )
      );
    } finally {
      setIsLoading(false);
    }
  };

  // Welcome message component
  const WelcomeMessage = () => (
    <div className="flex flex-col items-center justify-center h-full text-center p-8">
      <div className="bg-gradient-to-br from-indigo-500 to-purple-600 p-6 rounded-full mb-6 shadow-lg">
        <Brain size={48} className="text-white" />
      </div>
      <h2 className="text-2xl font-semibold text-slate-700 mb-2">Hello!</h2>
      <p className="text-slate-500 max-w-md">
        I'm your LLM Persona Tester. How can I help you today?
        Select a model and persona from the sidebar to get started.
      </p>
    </div>
  );

  return (
    <div className="flex flex-col h-full bg-slate-50 overflow-hidden"> {/* Changed main bg to slate-50 */}
      {/* Message display area */}
      <div className="flex-1 p-4 sm:p-6 space-y-4 overflow-y-auto">
        {messages.length === 0 && !isLoading ? (
          <WelcomeMessage />
        ) : (
          messages.map((msg) => (
            <div
              key={msg.id}
              className={`flex items-end max-w-xl lg:max-w-2xl xl:max-w-3xl
                          ${msg.sender === 'user' ? 'ml-auto justify-end' : 'mr-auto justify-start'}`}
            >
              {/* AI Avatar/Icon */}
              {msg.sender !== 'user' && (
                <div className={`flex-shrink-0 w-8 h-8 rounded-full mr-2.5 mb-1 flex items-center justify-center
                                ${msg.sender === 'ai' ? 'bg-indigo-500' : 'bg-red-500'}`}>
                  {msg.sender === 'ai' ? <Brain size={16} className="text-white" /> : <User size={16} className="text-white" />} {/* Changed error icon to User for placeholder */}
                </div>
              )}

              {/* Message Bubble */}
              <div
                className={`p-3 rounded-2xl break-words whitespace-pre-wrap text-sm shadow-sm
                            ${msg.sender === 'user'
                              ? 'bg-indigo-600 text-white rounded-br-none'
                              : msg.sender === 'ai'
                                ? 'bg-white text-slate-800 border border-slate-200 rounded-bl-none'
                                : 'bg-red-100 text-red-700 border border-red-200 rounded-bl-none' // Error style
                            }`}
              >
                {/* Display model name for AI messages (except "Thinking...") */}
                {msg.sender === 'ai' && msg.modelUsed && msg.text !== "Thinking..." && (
                  <p className="text-xs text-indigo-400 font-medium mb-1">{msg.modelUsed}</p>
                )}
                {msg.text}
              </div>
            </div>
          ))
        )}
        <div ref={messagesEndRef} />
      </div>
      <ChatInput onSendMessage={handleSendMessageFromChatBox} isLoading={isLoading} />
    </div>
  );
};
